.skills {
  &_content-wrapper {
    display: flex;
    padding: 20px;
    gap: 20px;

    &_inner-content {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: center;

      &_category-text {
        display: inline-block;
        color: var(--yellow-theme-main-color);
        font-size: 22px;
        font-weight: 700;
        line-height: 1rem;
        letter-spacing: 0.5px;
        position: relative;
        text-transform: uppercase;
        padding: 5px 10px;
        border-bottom: 2px solid;
      }
      &_category-text::before,
      &_category-text::after {
        position: absolute;
        content: "";
        width: 2px;
        height: 5px;
        background-color: var(--yellow-theme-main-color);
        bottom: -1px;
      }

      &_category-text::before {
        left: 0;
      }
      &_category-text::after {
        right: 0;
      }
      &_progressbar-container {
        width: 100%;
      }
      .progressbar-wrapper {
        padding: 30px 0px 0px 0px;
        p {
          font-size: 1.6rem;
          margin: 0 0 10px;
          font-weight: 500;
          text-transform: uppercase;
          color: var(--yellow-theme-sub-text-color);
        }
      }
    }
  }
}
@media only screen and (max-width: 768px) {
  .skills {
    &_content-wrapper {
      flex-direction: column;
    }
    .skills_content-wrapper_inner-content_progressbar-container {
      margin-bottom: 20px;
    }
  }
}
